#container {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

#top_div {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 50%;
  margin: 10px;
}

#bottom_div {
  position: absolute;
  top: 50%;
  left: 0;
  right:0;
  bottom: 0;
  margin: 10px;

}

.center_all{
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  position: absolute;

}

.question {
  font-family: "Calibre", sans-serif;
  font-size: clamp(20px, 1vw, 40px);
  font-weight: 400;
  text-align: center;
  color:#444452;
}


.button-margin{
  margin: 5px;
}
